import React, { useRef, useState, useEffect } from "react";
import { S } from "./ColorModelsDropDown.styles";
const models = ["hex", "rgb", "hsb"];
export const ColorModelsDropDown = ({ model, setModel }) => {
    const dropDownRef = useRef(null);
    const [isExpanded, setIsExpanded] = useState(false);
    useEffect(() => {
        const onClickOutside = () => {
            if (isExpanded)
                setIsExpanded(false);
        };
        document.addEventListener("click", onClickOutside, false);
        return () => {
            document.removeEventListener("click", onClickOutside, false);
        };
    }, [isExpanded]);
    const onTriggerClick = () => {
        setIsExpanded(!isExpanded);
    };
    const onModelClick = (model) => {
        setModel(model);
    };
    return (React.createElement(S.DropDown, { ref: dropDownRef },
        React.createElement(S.DropDownTrigger, { onClick: onTriggerClick }, model.toUpperCase()),
        React.createElement(S.DropDownMenu, { "aria-expanded": isExpanded }, models.map((model) => (React.createElement(S.DropDownMenuModel, { key: model, onClick: () => onModelClick(model) }, model.toUpperCase()))))));
};
